$(document).ready(function () {
	$('.slider').slick({
		dots: true,
		adaptiveHeight: true,
		variableWidth: true,
		autoplay: true,
		centerMode: true,
		autoplaySpeed: 800,
		appendArrows: $('.social__bottom-slider'),
		appendDots: $('.social__bottom-slider'),
		responsive: [
			{
				breakpoint: 576,
				settings: {
					lazyLoad: 'ondemand',
					slidesToShow: 1
				}
			}
		]
	});
});
$(document).ready(function () {
	$('.hamburger').click(function () {
		$('.hamburger, .menu').toggleClass('active');
		$('body').toggleClass('lock');
	});
});